@import url('https://fonts.googleapis.com/css2?family=Akshar&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,900&display=swap');

*{
  transition: all 0.4s ease-in-out ;
  padding:0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Akshar', sans-serif;
  scroll-behavior: smooth;
}



.body-main{
  overflow-x:hidden ;
  background-image: url('./bg.jpg');
  background-position: center;
  background-size: cover;
  color: snow;
  width: 100%;
}

.main-text{
  .explore-more{
    font-size: 40px;
    margin-top: 40px;
    text-shadow: -3px 3px rgb(255, 255, 255);
    color: rgb(119, 0, 255);
  }
  .explore{
    font-size: 20px;
    background-color: transparent;
    color: rgb(255, 0, 98);
    border: 1px solid rgb(255, 0, 98);
    box-shadow: 6px 6px rgb(255, 0, 98);
    text-shadow: none;
    cursor: pointer;
    transition: all 0.1s ease;
    padding:0 20px;
    &:hover{
      box-shadow: 0px 0px rgb(255, 0, 98);
    }
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  cursor: pointer;
  font-size: 200px;
  padding: 10px;
  line-height: 45%;
  text-shadow: -8px 8px rgb(255, 255, 255);
  display: flex;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  font-style: italic;
  color: rgb(98, 255, 98);
  transition: all 0.1s ease-out;
  .title-t:hover{
    text-shadow: 0px 0px rgb(255, 255, 255);
  }
  .title-t{
    transition: all 0.2s ease-out;
    text-shadow: 8px 8px rgb(255, 255, 255);
  }
  .title-r{
    transition: all 0.2s ease-out;
    &:hover{
      text-shadow: 0px 0px rgb(255, 255, 255);
    }
    margin-left: 30px;
  }
}

@media (max-width:768px){
  .main-text{
    .explore-more{
      font-size: 30px;
      margin-top: 40px;
      text-shadow: -3px 3px rgb(255, 255, 255);
