      text-shadow: none;
      cursor: pointer;
      transition: all 0.1s ease;
      padding:0 20px;
      &:hover{
        box-shadow: 0px 0px rgb(255, 0, 98);
      }
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    cursor: pointer;
    font-size: 140px;
    padding: 10px;
    line-height: 45%;
    text-shadow: -8px 8px rgb(255, 255, 255);
    display: flex;
    font-family: 'Roboto', sans-serif;
    font-weight: bolder;
    font-style: italic;
    color: rgb(98, 255, 98);
    transition: all 0.1s ease-out;
    .title-t:hover{
      text-shadow: 0px 0px rgb(255, 255, 255);
    }
    .title-t{
      transition: all 0.2s ease-out;
      text-shadow: 8px 8px rgb(255, 255, 255);
    }
    .title-r{
      transition: all 0.2s ease-out;
      &:hover{
        text-shadow: 0px 0px rgb(255, 255, 255);
      }
