@import url("https://fonts.googleapis.com/css2?family=Akshar&display=swap");
body {
  overflow-x: hidden;
}

.h1-about {
  color: aqua;
  font-size: 3rem;
}

@-webkit-keyframes rott {
  from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

@keyframes rott {
  from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

* {
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Akshar', sans-serif;
}

.cr {
  -webkit-animation: rott 10s linear infinite;
          animation: rott 10s linear infinite;
  background-image: url("./trex2.jpg");
  background-size: cover;
  background-position: center;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  display: inline-block;
}

.body-about {
  overflow-y: hidden;
  background-image: url("../../bg.jpg");
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  padding-top: 30px;
  width: 100vw;
  padding: 60px 20px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  align-items: center;
  justify-items: center;
}

.body-about .about-t {
  font-size: 30px;
  color: aquamarine;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.body-about .about-t span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0 10px;
}

.body-about p {
  border-bottom: 1px solid white;
  color: white;
  font-size: 30px;
  font-weight: bolder;
  text-align: start;
  margin: 20px 30px;
  line-height: 80px;
}

@media (min-width: 769px) {
  .body-about {
    overflow-y: hidden;
    background-image: url("../../bg.jpg");
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    width: 100vw;
    padding: 100px 50px;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .body-about .about-t {
    font-size: 30px;
    color: aquamarine;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .body-about p {
    color: whitesmoke;
    font-size: 40px;
    font-weight: bolder;
    text-align: start;
    margin: 20px 30px;
    line-height: 80px;
  }
}
