@import url("https://fonts.googleapis.com/css2?family=Akshar&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,900&display=swap");
* {
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Akshar', sans-serif;
  scroll-behavior: smooth;
}

.body-main {
  overflow-x: hidden;
  background-image: url("./bg.jpg");
  background-position: center;
  background-size: cover;
  color: snow;
  width: 100%;
}

.main-text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100vh;
  cursor: pointer;
  font-size: 200px;
  padding: 10px;
  line-height: 45%;
  text-shadow: -8px 8px white;
  display: flex;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  font-style: italic;
  color: #62ff62;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.main-text .explore-more {
  font-size: 40px;
  margin-top: 40px;
  text-shadow: -3px 3px white;
  color: #7700ff;
}

.main-text .explore {
  font-size: 20px;
  background-color: transparent;
  color: #ff0062;
  border: 1px solid #ff0062;
  -webkit-box-shadow: 6px 6px #ff0062;
          box-shadow: 6px 6px #ff0062;
  text-shadow: none;
  cursor: pointer;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
  padding: 0 20px;
}

.main-text .explore:hover {
  -webkit-box-shadow: 0px 0px #ff0062;
          box-shadow: 0px 0px #ff0062;
}

.main-text .title-t:hover {
  text-shadow: 0px 0px white;
}

.main-text .title-t {
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  text-shadow: 8px 8px white;
}

.main-text .title-r {
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  margin-left: 30px;
}

.main-text .title-r:hover {
  text-shadow: 0px 0px white;
}

