.menu-icon {
      position: absolute;
      z-index: 2000;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 45px;
      margin: 20px 30px;
      cursor: pointer;
      right: 0;

      &:hover .menu-part1 {
            width: 30px;
      }

      &:hover .menu-part3 {
            width: 30px;
      }

      .menu-part1,
      .menu-part2,
      .menu-part3 {
            display: inline-block;
            background-color: snow;
            height: 5px;
            border-radius: 1px;
            margin: 5px 0;
      }

      .menu-part1 {
            width: 15px;
            align-self: flex-end;
      }

      .menu-part2 {
            width: 30px;
      }

      .menu-part3 {
            width: 15px;
            align-self: flex-start;
      }
}

.nav-not-move {
      transform: translateX(-100%);
      position: fixed;
}

.nav-move {
      transform: translateX(0);
      position: fixed;
}

.nav-list2 {
      position: fixed;
      height: 100vh;
      z-index: 2000;
      width: 50vw;
      background-color: pink;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      list-style: none;
      transition: all 0.5s ease-in-out;
}


.nav-list3 {
      position: fixed;
      height: 100vh;
      z-index: 1999;
      width: 50vw;
      background-color: rgb(255, 255, 255);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      list-style: none;
      transition: all 0.3s ease-in-out;
}

.nav-list {
      position: fixed;
      height: 100vh;
      z-index: 2001;
      width: 50vw;
      background-color: #333;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      list-style: none;
      transition: all 0.9s ease-in-out;

      li {
            width: 30vw;
            display: flex;
            align-items: center;
            justify-content: center;

            &:hover a {
                  color: aqua;
            }

            &:hover {
                  animation: rubberBand 1s ease-in-out both;
            }
      }

      a {
            font-size: 40px;
            color: snow;
      }
}

@media (min-width:769px) {
      .nav-list2 {
            display: none;
      }

      .nav-list3 {
            display: none;
      }

      .menu-icon {
            display: none;
      }

      .nav-list {
            top: 0;
            left: 0;
            right: 0;
            position: absolute;
            z-index: 300;
            height: 30px;
            padding: 30px 35px;
            width: 100vw;
            background-color: #333;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            list-style: none;
            transform: translateX(0);

            li {
                  &:hover a {
                        color: aquamarine;
                  }

                  &:hover {
                        animation: tada 1s ease-in-out both;
                  }
            }

            a {
                  font-size: 25px;
                  color: snow;
            }
      }
}