@import url('https://fonts.googleapis.com/css2?family=Akshar&display=swap');

body{
      overflow-x: hidden;
}

.h1-about{
      color: aqua;
      font-size: 3rem;
}

@keyframes rott{
      from{transform: rotateZ(0deg);}
      to{transform: rotateZ(360deg);}
}

*{
  transition: all 0.5s ease-in-out ;
  padding:0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Akshar', sans-serif;
}

.cr{
            animation: rott 10s linear infinite;
            background-image: url('./trex2.jpg');
            background-size: cover;
            background-position: center;
            width: 250px;
            height: 250px;
            border-radius: 50%;
            display: inline-block;
}
.body-about{
      overflow-y: hidden;
      background-image: url('../../bg.jpg');
      background-size: cover;
      background-position: center;
      min-height: 100vh;
      padding-top: 30px;
.about-t{
      font-size: 30px;
      color: aquamarine;
      display:flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      span{
            display: flex;
            align-items: center;
            margin: 0 10px;
      }
}
      width: 100vw;
      padding: 60px 20px;
      display: grid;
      grid-template-columns: 1fr;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      align-items: center;
      justify-items: center;
      p{
            border-bottom: 1px solid white;
            color: white;
            font-size: 30px;
            font-weight: bolder;
            text-align: start;
            margin: 20px 30px;
            line-height: 80px;
      }
}

@media (min-width:769px){
      .body-about{
            overflow-y: hidden;
            background-image: url('../../bg.jpg');
            background-size: cover;
            background-position: center;
            min-height: 100vh;
            width: 100vw;
            padding: 100px 50px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .about-t{
                  font-size: 30px;
                  color: aquamarine;
                  display:flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: center;
            }
            p{
                  color: whitesmoke;
                  font-size: 40px;
                  font-weight: bolder;
                  text-align: start;
                  margin: 20px 30px;
                  line-height: 80px;
            }
      }
      }

